import {
    collection,
    addDoc,
    getDocs,
    getDoc,
    doc,
    query,
    where,
    orderBy,
    Timestamp,
    DocumentData,
    runTransaction,
    increment
} from 'firebase/firestore';
import { db } from './firebase';
import { Story } from '@/interfaces/Story';
import { Comment } from '@/interfaces/Comment';

// Firestore koleksiyon referanslarƒ±
const storiesCollection = collection(db, 'stories');
const commentsCollection = collection(db, 'comments');

// Hikayeleri getir
export async function getStories(): Promise<Story[]> {
    try {
        const q = query(storiesCollection, orderBy('createdAt', 'desc'));
        const querySnapshot = await getDocs(q);

        return querySnapshot.docs.map(doc => {
            const data = doc.data();
            return {
                id: doc.id,
                ...data,
                createdAt: data.createdAt?.toDate() || new Date(),
            } as Story;
        });
    } catch (error) {
        console.error('Error fetching stories:', error);
        return [];
    }
}

// Tek bir hikaye getir
export async function getStoryById(id: string): Promise<Story | null> {
    try {
        const stories = await getStories();
        return stories.find(story => story.id === id) || null;
    } catch (error) {
        console.error('Error fetching story:', error);
        return null;
    }
}

// Bir hikayeye ait yorumlarƒ± getir
export async function getCommentsByStoryId(storyId: string): Promise<Comment[]> {
    try {
        const q = query(
            commentsCollection,
            where('storyId', '==', storyId),
            orderBy('createdAt', 'desc')
        );
        const querySnapshot = await getDocs(q);

        const comments = querySnapshot.docs.map(doc => {
            const data = doc.data();
            return {
                id: doc.id,
                ...data,
                createdAt: data.createdAt?.toDate() || new Date(),
            } as Comment;
        });

        // Nested yorumlarƒ± organize et
        return organizeComments(comments);
    } catch (error) {
        console.error('Error fetching comments:', error);
        return [];
    }
}

// Yorumlarƒ± nested yapƒ±ya d√∂n√º≈üt√ºr
function organizeComments(comments: Comment[]): Comment[] {
    const commentMap = new Map<string, Comment>();
    const rootComments: Comment[] = [];

    // T√ºm yorumlarƒ± map'e ekle
    comments.forEach(comment => {
        commentMap.set(comment.id, { ...comment, replies: [] });
    });

    // Nested yapƒ±yƒ± olu≈ütur
    comments.forEach(comment => {
        const commentWithReplies = commentMap.get(comment.id);
        if (commentWithReplies) {
            // Eƒüer parent yoksa root comment
            rootComments.push(commentWithReplies);
        }
    });

    return rootComments;
}

// Yeni yorum ekle
export async function addComment(comment: Omit<Comment, 'id' | 'createdAt'>): Promise<string> {
    try {
        const docRef = await addDoc(commentsCollection, {
            ...comment,
            createdAt: Timestamp.now(),
        });
        return docRef.id;
    } catch (error) {
        console.error('Error adding comment:', error);
        throw error;
    }
}

// Yeni hikaye ekle
export async function addStory(story: Omit<Story, 'id' | 'createdAt'>): Promise<string> {
    try {
        const docRef = await addDoc(storiesCollection, {
            ...story,
            createdAt: Timestamp.now(),
        });
        return docRef.id;
    } catch (error) {
        console.error('Error adding story:', error);
        throw error;
    }
}

// Hikaye g√∂r√ºnt√ºlenme sayƒ±sƒ±nƒ± artƒ±r (Transaction ile g√ºvenli artƒ±rma)
export async function incrementStoryViews(storyId: string): Promise<void> {
    try {
        const storyRef = doc(db, 'stories', storyId);

        await runTransaction(db, async (transaction) => {
            const storyDoc = await transaction.get(storyRef);

            if (!storyDoc.exists()) {
                throw new Error('Story does not exist!');
            }

            // stats.views deƒüerini 1 artƒ±r
            transaction.update(storyRef, {
                'stats.views': increment(1)
            });
        });
    } catch (error) {
        console.error('Error incrementing story views:', error);
        // View count artƒ±rma hatasƒ± uygulamayƒ± durdurmamalƒ±
        // Sadece log'la ve devam et
    }
}
