import Link from 'next/link';
import { Story } from '@/interfaces/Story';

interface StoryCardProps {
    story: Story;
}

export default function StoryCard({ story }: StoryCardProps) {
    const formatDate = (date: Date) => {
        return date.toLocaleDateString('tr-TR', {
            year: 'numeric',
            month: 'long',
            day: 'numeric'
        });
    };

    const formatNumber = (num: number) => {
        if (num >= 1000) {
            return `${(num / 1000).toFixed(1)}k`;
        }
        return num.toString();
    };

    return (
        <Link href={`/story/${story.id}`} className="story-card">
            <div className="story-card-header">
                <span className="category-badge">{story.category}</span>
                <span className="story-date">{formatDate(story.createdAt)}</span>
            </div>

            <h3 className="story-title">{story.title}</h3>
            <p className="story-excerpt">{story.excerpt}</p>

            <div className="story-footer">
                <div className="author-info">
                    <span className="author-avatar">{story.author.avatar}</span>
                    <span className="author-name">{story.author.name}</span>
                </div>

                <div className="story-stats">
                    <span className="stat">
                        <span className="stat-icon">üëÅÔ∏è</span>
                        {formatNumber(story.stats.views)}
                    </span>
                    <span className="stat">
                        <span className="stat-icon">üí¨</span>
                        {formatNumber(story.stats.comments)}
                    </span>
                    <span className="stat">
                        <span className="stat-icon">‚ù§Ô∏è</span>
                        {formatNumber(story.stats.likes)}
                    </span>
                </div>
            </div>
        </Link>
    );
}
