'use client';

import { useEffect, useState } from 'react';
import StoryCard from './components/StoryCard';
import { Story } from '@/interfaces/Story';
import { getStories } from '@/lib/firestore';

export default function Home() {
  const [stories, setStories] = useState<Story[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function fetchStories() {
      try {
        const fetchedStories = await getStories();
        setStories(fetchedStories);
      } catch (error) {
        console.error('Error loading stories:', error);
      } finally {
        setLoading(false);
      }
    }

    fetchStories();
  }, []);

  return (
    <div className="home-page">
      <div className="hero-section">
        <div className="hero-content">
          <h1 className="hero-title">
            <span className="gradient-text">Fabula</span>
          </h1>
          <p className="hero-subtitle">
            Hikayelerin bulu≈üma noktasƒ±. Oku, yorum yap, payla≈ü.
          </p>
        </div>
      </div>

      <main className="main-content">
        <div className="stories-header">
          <h2>‚ú® Pop√ºler Hikayeler</h2>
          <p>Topluluk tarafƒ±ndan en √ßok okunan ve beƒüenilen hikayeler</p>
        </div>

        {loading ? (
          <div className="loading-state">
            <div className="spinner"></div>
            <p>Hikayeler y√ºkleniyor...</p>
          </div>
        ) : stories.length === 0 ? (
          <div className="empty-state">
            <span className="empty-icon">üìö</span>
            <p>Hen√ºz hikaye yok. ƒ∞lk hikayeyi siz ekleyin!</p>
          </div>
        ) : (
          <div className="stories-grid">
            {stories.map((story) => (
              <StoryCard key={story.id} story={story} />
            ))}
          </div>
        )}
      </main>
    </div>
  );
}

